import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { fetchOrderedProduct } from "../redux";
import ProductInfo from "./ProductInfo";
import { CHECKOUT_URL } from '../Urls';

const Cart = () => {
  const cart = useSelector((state) => state.cart.data);
  const dispatch = useDispatch();
  useEffect(() => {
    document.getElementById("header").innerHTML = "E Commerce | Cart";
  }, []);

  const handleBuyProduct = () => {
    dispatch(fetchOrderedProduct(cart));
  };

  return (
    <div className="container">
      {!cart.length ? (
        <p className="mt-3">Your Cart is Empty</p>
      ) : (
        <div>
          {cart.map((product, index) => {
            return <ProductInfo key={index} product={product} />;
          })}
          <Link to={CHECKOUT_URL}>
            <button
              className="btn btn-info mx-5 mb-3"
              onClick={() => handleBuyProduct()}
            >
              Buy Now
            </button>
          </Link>
        </div>
      )}
    </div>
  );
};

export default Cart;
