import {
    FETCH_SELECTED_PRODUCT_REQUEST,
    FETCH_SELECTED_PRODUCT_SUCCESS,
    FETCH_SELECTED_PRODUCT_FAILURE
} from './selectedProductTypes';

export const fetchSelectedProduct = (productId) => {
    return (dispatch) => {
        dispatch(fetchSelectedProductRequest);
        import('../../mockProductData')
            .then(data => data.default.find(item => item.isbn === productId ))
            .then(data => {
                dispatch(fetchSelectedProductSuccess(data))
            })
            .catch(error => dispatch(fetchSelectedProductFailure(error)))
    }
}

export const fetchSelectedProductRequest = () => {
    return {
        type: FETCH_SELECTED_PRODUCT_REQUEST
    }
}

export const fetchSelectedProductSuccess = (product) => {
    return {
        type: FETCH_SELECTED_PRODUCT_SUCCESS,
        payload: product
    }
}

export const fetchSelectedProductFailure = (error) => {
    return {
        type: FETCH_SELECTED_PRODUCT_FAILURE,
        payload: error
    }
}